import React from 'react'
import Coffee from './coffee.jpg'
import LondonBus from './LondonBus.JPG'
import Turing from './turing.jpg'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Image from 'react-bootstrap/Image'
import './Photos.css'

const Photos = () => (
    <div>
    <br/>
        <Container className='PhotographyDiv'>
        	<Row>
	        	<Col md={12}>
	        		<div className='photographyText'>I like playing with cameras too...</div>
	        	</Col>
        		<Col xs={12} md={4}>
	        		<Image className='photography' src={Coffee} alt='Coffee' fluid/>
	        	</Col>
	        	<Col xs={12} md={4}>
	        		<Image className='photographyVert' src={Turing} alt='Turing Machine' fluid/>
	        	</Col>
	        	<Col xs={12} md={4}>
	        		<Image className='photography' src={LondonBus} alt='London Bus' fluid/>
	        	</Col>
	        </Row>
        </Container>
    </div>
)
export default Photos